import React, { lazy, useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { fireAuth } from "../fireApi";
import logo from './garuda.png'
import {
  CHeader,
  CToggler,
  CImg,
  CHeaderBrand,
  CHeaderNav,
  CHeaderNavItem,
  CHeaderNavLink,
  CButton,
  CSubheader,
  CBreadcrumbRouter,
  CLink,
  CSelect
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import store from '../redux/store'

// routes config
import routes from '../routes'

import { 
  TheHeaderDropdown,
  TheHeaderDropdownMssg,
  TheHeaderDropdownNotif,
  TheHeaderDropdownTasks
}  from './index'

const TheHeader = () => {
  const dispatch = useDispatch()
  const sidebarShow = useSelector(state => state.changeState.sidebarShow)
  const userRole = useSelector(state => state.userRole.userRole)

  const toggleSidebar = () => {
    const val = [true, 'responsive'].includes(sidebarShow) ? false : 'responsive'
    dispatch({type: 'set', sidebarShow: val})
  }

  const toggleSidebarMobile = () => {
    const val = [false, 'responsive'].includes(sidebarShow) ? true : 'responsive'
    dispatch({type: 'set', sidebarShow: val})
  }
  const logOut = () => fireAuth.signOut().then(function() {
    console.log("sukses logout")
  }).catch(function(error) {
    console.log(error)
  });
  console.log(userRole)
  return (
    <CHeader style={{backgroundColor: "#02275d"}} withSubheader>
      <CHeaderBrand className="mx-auto d-lg-none" to="/">
        <CIcon name="logo" height="48" alt="Logo"/>
      </CHeaderBrand>

      <CHeaderNav>
        <div className="pl-4 p-2">
        <CImg
          className="c-sidebar-brand-minimized"
          src={logo}
          width="5%"
        />          
        </div>
        {/* <div className="px-3">
        <CButton onClick={logOut.bind(this)} size="sm" color="danger">Logout</CButton>          
        </div>         */}
      </CHeaderNav>
      {/* <CHeaderNav className="px-3"> */}
      {/* <TheHeaderDropdownNotif/>
        <TheHeaderDropdownTasks/>
        <TheHeaderDropdownMssg/>
        <TheHeaderDropdown/> */}
      {/* </CHeaderNav> */}

      <CSubheader className="px-3 justify-content-between">
        <div>
        <CBreadcrumbRouter 
          className="border-0 c-subheader-nav m-0 px-0 px-md-3" 
          routes={routes}
        />
        </div>
        <div className="d-flex align-items-center">
          <div className="pr-1 pt-2">
            <h6>
              {userRole == "1" ?
                "USER PELAKU USAHA":
                userRole == "2" ?
                "USER KEMENKO":
                userRole == "3" ?
                "USER K/L":
                userRole == "4"?
                "USER SURVEYOR" :
                userRole == "5" ?
                "USER ADMIN TEKNIS" :
                "USER KEMENPERIN" 
              }           
          </h6>              
          </div>
          <div className="d-flex align-items-start">
          <CIcon size={'lg'}name={'cilUser'} />
          </div>       
        </div>
      </CSubheader>
    </CHeader>
  )
}

export default TheHeader
